import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import plate from './plate.jpg';
import maccheese from './maccheese.jpg';
import turkeycass from './turkeycass.jpg';
import cranberry from './cranberry.jpg';
import antslog from './antslog.jpg';
import turkey from './turkey.jpeg';
import nachos from './nachos.jpg';
import cinnamon from './cinnamon.jpg';
import sugarcookies from './sugarcookies.jpg';
import hummus from './hummus.jpg';
import chickensoup from './chickensoup.jpg';
import caramel from './caramel.jpg';
import candied from './candied.jpg';
import juicepop from './juicepop.JPG';
import hotdog from './hotdog.jpg';
